import styled from "styled-components"

export const Main = styled.main`
  display: flex;
  /* & .main-header {
    display: "flex";
  } */
  & .main-header {
    display: flex;
    align-items: center;
    padding: 0 32px;
    min-height: 48px;
    box-shadow: inset 0px -1px 0px ${({ theme }) => theme.separator_non_opaque};
  }
  & .main-content {
    position: relative;
    //margin-left: 360px;
    padding: 32px;
    width: 100%;
    max-width: 1200px;
    @media (max-width: 1400px) {
      margin-left: 0;
      max-width: 100%;
    }
    @media (max-width: 576px) {
      padding: 16px 0;
    }
  }
`

