import React, { useState, useEffect } from "react"
import Sidebar from "organisms/Sidebar"
import * as S from "./styles"
import { ThemeProvider } from "styled-components"
import { Link } from "gatsby"
import {
  lightTheme,
  darkTheme,
  elevatedDarkTheme,
} from "src/global-styles/theme"
import { GlobalStyles } from "src/global-styles"
import { useWindowWidth } from "libs/helpers"

export default function Layout(props) {
  let sidebarHeaderHeight,
    sidebarFooterHeight,
    sidebarHeaderCompensator,
    sidebarFooterCompensator

  const windowWidth = useWindowWidth()

  useEffect(() => {
    sidebarHeaderHeight = document.querySelector(".sidebar-header").clientHeight
    sidebarHeaderCompensator = document.querySelector(
      ".main-header-compensator"
    )

    if (windowWidth <= 1400)
      sidebarHeaderCompensator.style.height = `${sidebarHeaderHeight}px`

    sidebarFooterHeight = document.querySelector(".sidebar-footer").clientHeight
    sidebarFooterCompensator = document.querySelector(
      ".main-footer-compensator"
    )
    sidebarFooterCompensator.style.height = `${sidebarFooterHeight * 2}px`
  })

  return (
    <>
      <ThemeProvider theme={lightTheme}>
        <GlobalStyles />

        <S.Main className={props.className}>
          <Sidebar />
          <div className="main-content">
            <div className={"main-header-compensator"}></div>
            {props.children}
            <div className={"main-footer-compensator"}></div>
          </div>
        </S.Main>
      </ThemeProvider>
    </>
  )
}
